import React, { useState } from 'react';

import { ButtonGroup, ToggleButton } from 'react-bootstrap';
// import { propTypes } from 'react-bootstrap/esm/Image';
import DatalistInput, { useComboboxControls } from 'react-datalist-input';
import 'react-datalist-input/dist/styles.css';

function AgeDataList(props) {
  // const [radioValue, setRadioValue] = useState('male');

  const { setAge, value } = useComboboxControls({ initialValue: '10' });

  return (
    <>
      <DatalistInput
        value={value}
        setAge={setAge}
        label="Select ice cream flavor"
        showLabel={false}
        items={[
          { id: 0, value: '0' },
          { id: 1, value: '1' },
          { id: 2, value: '2' },
          { id: 3, value: '3' },
          { id: 4, value: '4' },
          { id: 5, value: '5' },
          { id: 6, value: '6' },
          { id: 7, value: '7' },
          { id: 8, value: '8' },
          { id: 9, value: '9' },
          { id: 10, value: '10' },
          { id: 11, value: '11' },
          { id: 12, value: '12' },
          { id: 13, value: '13' },
          { id: 14, value: '14' },
          { id: 15, value: '15' },
          { id: 16, value: '16' },
          { id: 17, value: '17' },
        ]}
        onSelect={(item) => {
          setAge(''); // Custom behavior: Clear input field once a value has been selected
        }}
      />
    </>
  );
}

export default AgeDataList;